// Discover
.discover {
  .world {
    padding: 0 34px;
    position: relative;
    img {
      transition: all .5s;
      filter: grayscale(100%);
    }
    &:hover {
      img {
        transform: translateY(-8px);
        filter: grayscale(0);
      }
    }
    .comany {
      position: absolute;
      bottom: -30px;
      right: 4px;
      background: #ea3a60;
      padding: 26px 50px;
      
      h4 {
        color: $white;
        text-transform: none;
      }
      p {
        color: $offwhite;
        margin: 12px 0;
      }
      span {
        color: $offwhite;
        cursor: pointer;
      }
    }
  }
}

// Attributio
.attribution {
  @include background('../img/v1.png');
  position: relative;
  z-index: 50;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color:rgba(41,55,66,0.8);
    z-index: -1;
  }
  .title {
    h2 {
      color: $white;
    }
  }
  .attrib {
    padding: 20px;
    border: 1px solid $primary-color;
    margin: 40px 30px 20px;
    border-radius: 5px;
    box-shadow: 0 4px 10px 0px #191919;
    width: 25em;
    .heding {
      svg {
        color: $white;
        font-size: 60px;
        margin: 10px 0 35px;
      }
      h5 {
        color: $white;
        font-size: 18px;
        margin-bottom: 30px;
      }
      p {
        color: $offwhite;
        letter-spacing: .5px;
        margin-bottom: 10px;
      }
    }
  }
}

// Our Services
.our-services {

  .services {
    transition: all .5s;
    position: relative;
    margin: 0 16px 28px;
    .heding {
      position: relative;
      &:after {
        content: '';
        position: absolute;
        top: 50px;
        left: 0;
        right: 0;
        bottom: 95px;
        background:#1919198f;
        opacity: 0;
      }
      img {
        position: relative;
        
      }
      svg {
        opacity: 0;
        position: absolute;
        left: calc(50% - 30px);
        top: 43%;
        color: #fff;
        font-size: 65px;
        z-index: 20;
        transition: all .5s;
      }
      h5 {
        font-size: 19px;
        padding: 25px 14px;
        background: black;
        color: #fff;
        text-transform: none;
        font-weight: normal;
        letter-spacing: 0;
        transition: all .7s;
      }
    }
    &:hover {
      .heding {
        &:after {
          top: 0;
          opacity: 1;
          transition: all .3s;
        }
      }
      svg {
        opacity: 1;
        top: 40%;
      }
      h5 {
        background: #ea3a60;
      }
    }
  }
}

// Looking 
.looking {
  background: linear-gradient(-45deg, #3657d9,#5452c5,#744daf,#a8458c,#d03e71);
  h4 {
    letter-spacing: normal;
    display: inline-block;
    color: $white;
    margin-top: 10px;
    margin-bottom: 0;
  }
  button {
    float: right;
  }
}

// Choose
.choose {

  .chose {
    margin-top: 50px;
    svg {
      width: 11%;
      color: $primary;
      font-size: 38px;
      margin-right: 10px;
    }
    .info {
      float: right;
      width: 85%;
      h3 {
        font-size: 22px;
      }
      p {
        font-size: 13px;
      }
    }
  }
}

// Number
.number {
  @include background('../img/number.jpg');
  @extend .attribution;
  &::after {
    background-color: $primary-color;
  }
  .numb {
    border-left: 1px solid $offwhite;
    padding-left: 60px;
    h2 {
      color: $white;
      font-size: 47px;
    }
    p {
      color: $offwhite;
      font-size: 18px;
      margin-bottom: 0;
    }
  }
}

// People
.People {
  .carousel-indicators {
    bottom: -40px;
    .oll {
      width: 12px;
      height: 12px;
      margin-right: 5px;
      margin-left: 5px;
      text-indent: -999px;
      background-color: transparent;
      border: 2px solid;
      transform: rotate(45deg) scale(.5);
    }
    .active {
      border-color: $primary;
      transform: rotate(45deg) scale(.7);
    }
  }
  .items {

    .fotolia {
      width: 20%;
      float: left;
      margin-right: 60px;
      img {
        padding-right: 50px;
      }
      .cone {
        position: relative;
        top: -122px;
        right: 0;
        left: 192px;
        bottom: 0;
        &::before {
          content: '';
          position: absolute;
          bottom: 9px;
          right: 192px;
          width: 2px;
          height: 58px;
          background: $offwhite;
        }
        &::after {
          content: '';
          position: absolute;
          right: 192px;
          width: 2px;
          height: 58px;
          background: $offwhite;
          top: 44px;
        }
        svg {
          position: absolute;
          font-size: 28px;
          margin-left: 15px;
          color: $primary;
        }
      }
      p {
        color: $title-color;
        font-size: 19px;
        margin-bottom: 0;
        margin-top: 20px;
      }
      span {
        font-size: 15px;
      }
    }
  }
  .pree {
    margin-top: 30px;
    line-height: 34px;
    letter-spacing: .5px;
    font-size: 18px;
  }
}

// Cuntact Us
.contact-us {
  @include background('../img/contact.jpg');
  position: relative;
  z-index: 50;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color:rgba(41,55,66,0.8);
    z-index: -1;
  }
  h2 {
    color: #fff;

  }
  form {
    width: 60%;
    margin-top: 60px;
    input[type="text"] {
      width: 38%;
      margin-bottom: 50px;
      margin-right: 60px;
      background: transparent;
      border: 0;
      border-bottom: 2px solid $primary;
      color: #fff;
      padding: 15px 0;
      outline: 0;
      &:nth-last-of-type(1) {
        display: block;
        width: calc(76% + 70px);
      }
    }
    button {
      padding-left: 30px;
      padding-right: 30px;
    }
  }
}






