// Navbar
nav {
  background: none;
  padding: 0 !important;
  margin: 0;
  z-index: 9999999999999;
  height: 0;
   .padding-t {
    padding-top: 10%;
   }
  .navbar-nav {
    .nav-item {
      .nav-link {
        color: $white;
        padding: 0 16px;
        font-size: 18px;
        font-weight: 600;
        span {
          display: block;
          width: 0px;
          background: $primary-color;
          height: 3px;
          margin-top: 7px;
          transition: all .3s;
        }
        &:hover {
          color: $white;
        }
        &:active {
          color: $white;
        }
      }
        &.active a span {
          width: 100%;
        }
        &:hover a span {
          width: 100%;
        }
      
    }
  }
}

// Header 
header {
  .wtc {
    position: absolute;
    h2 {
      color: $white;
      letter-spacing: 2px;
    }
    p {
      margin: 30px 0;
      line-height: 26px;
      letter-spacing: 1px;
      color: $offwhite;
    }
    
  }
  .heads1 {
    top: 40%;
    left: 14%;
    
  }
  .heads2 {
    top: calc(50% - 10px);
    text-align: center;
    margin: auto;
    width: 100%;
    h2 {
      margin-bottom: 60px;
    }
  }
  .heads3 {
    top: 40%;
    right: 14%;
    text-align: end;
    .read {
      &::before {
        content: '';
        transform: scaleX(1);
      }
      &:hover,
      &:focus,
      &:active {
        &::before {
          transform: scaleX(0);
        }
      }
    }
  }
}

@media (min-width: 576px) and (max-width: 991.98px){

  // Navbar
  nav {
    .padding-t {
      padding-top: 40px;
      .navbar-toggler {
        border: 0;
        outline: 0;
      }
      svg {
        color: #fff;
        font-size: 35px;
      }
     }
    .navbar-nav {
      background: #ea3a60;
      .nav-item {
        .nav-link {
          padding: 10px 0;
          &:hover {
            color: $offwhite;
          }
          span {
            display: none;
          }
        }
      }
    }
  }

  // Header 
  header {
    .wtc {
      h2 {
        letter-spacing: normal !important;
        font-size: 26px;
      }
      p {
        letter-spacing: normal !important;
      }
    }
    .heads1 {
      top: 35%;
    }
    .heads2 {
      top: 42%;
      h2 {
        margin-bottom: 40px;
      }
    }
    .heads3 {
      top: 35%;
    }
  }
}

@media (max-width: 575.98px) {
  // Navbar
  nav {
    .padding-t {
      padding-top: 20px;
      .navbar-toggler {
        border: 0;
        outline: 0;
      }
      svg {
        color: #fff;
        font-size: 35px;
      }
    }
    .navbar-nav {
      background: #ea3a60;
      .nav-item {
        .nav-link {
          padding: 10px 0;
          &:hover {
            color: $offwhite;
          }
          span {
            display: none;
          }
        }
      }
    }
  }

  // Header 
  header {
    .wtc {
      top: 40%;
      left: 0;
      right: 0;
      h2 {
        letter-spacing: normal !important;
        font-size: 24px;
        text-align: center;
        margin: auto;
        width: 90%;
      }
      p {
        display: none;
      }
      button {
        display: none;
      }
    }
    
  }

}