@media (min-width: 992px) and (max-width: 1199.98px) {
  //discover
  .discover {
    .world {
      margin-bottom: 60px;
    }
  }

  // People
  .People {
    .items {
      .fotolia {
        .cone {
          &::before {
            bottom: -13px;
            height: 45px;
          }
          &::after {
            height: 45px;
            top: 57px;
          }
          svg {
            top: 22px;
            margin-left: -21px;
          }
        }
      }
    }
  }
}

@media (min-width: 576px) and (max-width: 991.98px) {
  //discover
  .discover {
    .title {
      h2 {
        font-size: 22px;
        letter-spacing: normal;
      }
      p {
        font-size: 14px;
        margin-bottom: 40px;
      }
    }
    .world {
      margin-bottom: 60px;
    }
  }

  
  // Choose
  .choose {
    .chose {
      svg {
        width: 12%;
        color: $primary;
      }
      .info {
        float: right;
        width: 80%;
        h3 {
          font-size: 24px;
        }
      }
    }
  }

  // Number
  .number {
    .numb {
      margin-bottom: 50px;
    }
  }

  // People
  .People {
    .items {
      .fotolia {
        width: 100%;
        float: none;
        margin-right:0;
        text-align: center;
        img {
          padding-right: 0;
        }
        .cone {
          display: none
          
        }
      }
    }
    .pree {
      line-height: 28px;
      text-align: center;
    }
  }

  // Cuntact Us
  .contact-us {
    background-size: 100% 100%;
    form {
      width: 100%;
      
    }
  }

  // Looking
  .looking {
    text-align: center;
    h4 {
      margin-bottom: 30px;
    }
    button {
      float: none;
    }
  }
}


@media (min-width: 576px) and (max-width: 767.98px) {
  // Number
  .number {
    .numb {
      border-left:0;
      
      padding-left: 0px;
      text-align: center;
    }
  }

  // Cuntact Us
  .contact-us {
    form {
      width: 100%;
      overflow: hidden;
      input[type="text"] {
        width: 100%;
        &:nth-last-of-type(1) {
          width: 100%;
        }
      }
    }
  }
}


