.but-bord {
    margin-right: 18px;
    background: transparent;
    border: 2px solid $primary;
    color: $white;
    padding: 10px 26px;
    font-size: 18px;
    outline: 0;
    transform: perspective(1px) translateZ(0);
    transition: all .4s ease-out;
    
    position: relative;
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      background-color: $primary;
      transform: scaleX(0);
      transition: all .4s ease-out;
    }
    &:hover,
    &:focus,
    &:active {
      &::before {
        transform: scaleX(1);
      }
    }
  }

  .read {
    &::before {
      content: '';
      transform: scaleX(1);
    }
    &:hover,
    &:focus,
    &:active {
      &::before {
        transform: scaleX(0);
      }
    }
  }