footer {
  background: #232323ed;
  margin: 0;
  padding-top: 60px;
  padding-bottom: 1px;
  .socil {
    border-right: 1px solid #a0a0a0;
    margin-right: 15px;
    li { 
      margin-bottom: 16px;
      a {
        color: #a0a0a0;
        font-size: 18px;
        svg {
          margin-right: 20px;
        }
      }
    }
  }
  p {
    text-align: center;
    margin-top: 30px;
    background: #201f1f70;
    margin-bottom: 0;
    padding: 10px 0;
    color: #a0a0a0;
  }
  span {
    color: #f3f3f3;
  }
}

@media (min-width: 576px) and (max-width: 991.98px) {

  footer {
    .socil {
      border-right:0;
      border-bottom: 1px solid;
      width: 60%;
    }
  }
}

@media (max-width: 575.98px) {
  footer {
    .socil {
      border-right:0;
      border-bottom: 1px solid;
      width: 100%;
    }
  }
}