// Class

.pd-tb{
  padding-top: 80px;
  padding-bottom: 80px;
}
.m-t {
  margin-top: 80px;
}
.m-b {
  margin-bottom: 80px;
}
.mb {
  margin-bottom: 30px;
}



// Tags
a {
  &:hover {
    color: #fff;
    text-decoration: none;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $title-font;
  font-weight: 600;
  color: $title-color;
  text-transform: uppercase;
  letter-spacing: 2px;
}

p,
span {
  font-family: $text-font;
  color: $text-color;
}

// Title 
.title {
  text-align: center;
  h2{
    &:after {
      content: '';
      border-bottom: 3px solid $primary;
      border-radius: 25%;
      margin: 10px auto 20px;
      width: 35px;
      display: block;
    }
  }  
  p {
    width: 54%;
    margin: 0 auto 60px;
  }
}

.navbar-light .navbar-nav .active>.nav-link {
  color : #fff;
}


@media (min-width: 576px) and (max-width: 991.98px) {

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-weight: 600;
    letter-spacing: normal;
  }

  .mr0 {
    margin-bottom: 0 !important;
  }

}

@media (max-width: 575.98px) {
  .mr0 {
    margin-bottom: 0 !important;
  }
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-weight: 600;
    letter-spacing: normal;
  }
  .title {
    h2{
      &:after {
        content: '';
        border-bottom: 3px solid $primary;
        border-radius: 25%;
        margin: 10px auto 20px;
        width: 35px;
        display: block;
      }
    }  
    p {
      width: auto;
      margin: 0 auto 60px;
    }
  }
}